@font-face {
  font-family: 'BalsamiqSansRegular';
  src: local('BalsamiqSansRegular'),
       url('./fonts/BalsamiqSans/BalsamiqSans-Regular.ttf') format('truetype'),
}


body {
  margin: 0;
  font-family: "Balsamiq Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;

}

